.portfolio-image-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 220px;
}

.portfolio-image-container img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.overlay {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
  opacity: 0;
  transition: all 0.3s ease-in-out;
  display: flex;
  align-items: center;
  justify-content: center;
}

.portfolio-image-container:hover .overlay {
  opacity: 1;
}

.portfolio-item-meta {
  padding: 20px;
  text-align: center;
  color: white;
}

.portfolio-item-meta h5 {
  font-size: 18px;
  margin-bottom: 8px;
  color: #fff;
}

.portfolio-item-meta p {
  font-size: 14px;
  line-height: 1.6;
  color: #ccc;
  margin-bottom: 15px;
}

.project-links {
  display: flex;
  gap: 15px;
  justify-content: center;
  margin-top: 15px;
}

.project-link {
  display: inline-flex;
  align-items: center;
  padding: 8px 15px;
  border-radius: 4px;
  color: #fff;
  text-decoration: none;
  font-size: 14px;
  transition: all 0.3s ease;
}

.project-link i {
  margin-right: 5px;
}

.github-link {
  background-color: #333;
}

.github-link:hover {
  background-color: #24292e;
}

.demo-link {
  background-color: #0762f9;
}

.demo-link:hover {
  background-color: #0056b3;
}

.project-title {
  text-align: center;
  margin-top: 10px;
  font-weight: bold;
  color: #333;
}
